import React from "react";
import resumeFile from "../documents/resume.pdf";

const Resume = ({ classicHeader, darkTheme }) => {
  const educationDetails = [
    {
      yearRange: "2017 - 2023",
      title: "Computer Science Engeneering",
      place: "UDELAR",
      desc: "5 year degree in Computer Science Engeneering aquiring solid knowledge in Computer Science and Mathematics",
    },
    {
      yearRange: "2022",
      title: "Exchange Program in Madrid",
      place: "Universidad Complutense de Madrid",
      desc: "A 1 semester exchange program in Madrid taking Blockchain and Quantum Computing courses",
    },
    {
      yearRange: "Continuously",
      title: "Micro Courses",
      place: "Coursera, Udemy, Udacity, etc",
      desc: "Taking micro courses in different topics such as Blockchain, Typescript, Docker, etc",
    },
  ];

  const experienceDetails = [
    {
      yearRange: "2018 - 2021",
      title: "Fullstack Developer",
      place: "Biller (Uruguay)",
      desc: "With several years of experience as a Full Stack Developer, I have honed my skills in developing and maintaining web applications. I have a deep understanding of both front-end and back-end technologies, including HTML, CSS, JavaScript, and various back-end frameworks and databases. My experience has taught me the importance of creating clean, scalable, and secure code, and I have a passion for delivering high-quality, user-friendly products.",
    },
    {
      yearRange: "2022 - Present",
      title: "Senior Fullstack Developer",
      place: "Gumpo (United Kingdom)",
      desc: "Working as a Senior Fullstack Developer in a mostly English speaking environment, I have been able to improve my English skills. I have been working in the frontend and backend of the application, using ReactJS, NextJS, NodeJS, Express, MySQL, and Docker.",
    },
  ];

  const skills = [
    {
      name: "Frontend Development",
      percent: 90,
    },
    {
      name: "Backend Development",
      percent: 95,
    },
    {
      name: "Javascript",
      percent: 90,
    },
    {
      name: "NextJS",
      percent: 85,
    },
    {
      name: "Databases",
      percent: 85,
    },
    {
      name: "Blockchain",
      percent: 50,
    },
  ];

  return (
    <section
      id="resume"
      className={"section " + (darkTheme ? "bg-dark-1" : "")}
    >
      <div className={"container " + (classicHeader ? "" : "px-lg-5")}>
        {/* Heading */}
        <div className="position-relative d-flex text-center mb-5">
          <h2
            className={
              "text-24  text-uppercase fw-600 w-100 mb-0 " +
              (darkTheme ? "text-muted opacity-1" : "text-light opacity-4")
            }
          >
            Summary
          </h2>
          <p
            className={
              "text-9 text-dark fw-600 position-absolute w-100 align-self-center lh-base mb-0 " +
              (darkTheme ? "text-white" : "text-dark")
            }
          >
            {" "}
            Resume
            <span className="heading-separator-line border-bottom border-3 border-primary d-block mx-auto" />
          </p>
        </div>
        {/* Heading end*/}
        <div className="row gx-5">
          {/* My Education */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Education
            </h2>
            {educationDetails.length > 0 &&
              educationDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
          {/* My Experience */}
          <div className="col-md-6">
            <h2
              className={
                "text-6 fw-600 mb-4 " + (darkTheme ? "text-white" : "")
              }
            >
              My Experience
            </h2>
            {experienceDetails.length > 0 &&
              experienceDetails.map((value, index) => (
                <div
                  key={index}
                  className={
                    "bg-white  rounded p-4 mb-4 " +
                    (darkTheme ? "bg-dark" : "bg-white border")
                  }
                >
                  <p className="badge bg-primary text-2 fw-400">
                    {value.yearRange}
                  </p>
                  <h3 className={"text-5 " + (darkTheme ? "text-white" : "")}>
                    {value.title}
                  </h3>
                  <p className={darkTheme ? "text-primary" : "text-danger"}>
                    {value.place}
                  </p>
                  <p className={"mb-0 " + (darkTheme ? "text-white-50" : "")}>
                    {value.desc}
                  </p>
                </div>
              ))}
          </div>
        </div>
        {/* My Skills */}
        <h2
          className={
            "text-6 fw-600 mt-4 mb-4 " + (darkTheme ? "text-white" : "")
          }
        >
          My Skills
        </h2>
        <div className="row gx-5">
          {skills.length > 0 &&
            skills.map((skill, index) => (
              <div key={index} className="col-md-6">
                <p
                  className={
                    " fw-500 text-start mb-2 " +
                    (darkTheme ? "text-light" : "text-dark")
                  }
                >
                  {skill.name}{" "}
                  <span className="float-end">{skill.percent}%</span>
                </p>
                <div
                  className={
                    "progress progress-sm mb-4 " + (darkTheme ? "bg-dark" : "")
                  }
                >
                  <div
                    className="progress-bar"
                    role="progressbar"
                    style={{ width: skill.percent + "%" }}
                    aria-valuenow={skill.percent}
                    aria-valuemin={0}
                    aria-valuemax={100}
                  />
                </div>
              </div>
            ))}
        </div>
        <div className="text-center mt-5">
          <a
            className="btn btn-outline-secondary rounded-pill shadow-none"
            href={resumeFile}
            download="Luis Bonomi Resume.pdf"
          >
            Download CV
            <span className="ms-1">
              <i className="fas fa-download" />
            </span>
          </a>
        </div>
      </div>
    </section>
  );
};

export default Resume;
